:root {
  --nav-height: 40px;
  background: #171717;
}
body {
  margin: 0;
  height: 100vh;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: var(--nav-height);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  top: 20px;
}
.nav a {
  margin-left: 24px;
  text-decoration: none;
  color: white;
  font-size: 14px;
  letter-spacing: 0.5px;
  transition: opacity 0.3s ease;
}
.nav a:hover {
  opacity: 0.7;
}
.logo-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo {
  max-width: 200px;
  width: 100%;
  height: auto;
  transform: scale(6);
}
.works-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1px;
  background: #f0f0f0;
}
.work-item {
  aspect-ratio: 1/1;
  position: relative;
  overflow: hidden;
  background: white;
}
.work-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}
.work-item:hover img {
  transform: scale(1.03);
}
.work-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  background: rgba(0,0,0,0.6);
  color: white;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}
.work-item:hover .work-caption {
  transform: translateY(0);
}
@media (max-width: 768px) {
  .works-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-border-style: solid;
    }
  }
}